import React, { useState, useEffect } from 'react';
import dayjs from 'dayjs';
import 'dayjs/locale/ko';
import axios from 'axios';
import TodoForm from './TodoForm';
import TodoList from './TodoList';
import TodayTimeline from './TodayTimeline';
import './App.css';

dayjs.locale('ko');

const API_URL = 'http://localhost:5000/todos';

//  yyyy-mm-dd   
function formatDate(date) {
  if (!date) return '';
  return dayjs(date).format('YYYY-MM-DD');
}

function App() {
  //  todos
  const [todos, setTodos] = useState([]);
  //  
  const [loading, setLoading] = useState(false);

  //   todos 
  useEffect(() => {
    fetchTodos();
  }, []);

  // todos   
  const fetchTodos = async () => {
    setLoading(true);
    try {
      const res = await axios.get(API_URL);
      setTodos(res.data);
    } catch (e) {
      alert('   .');
    }
    setLoading(false);
  };

  //    todos
  const todayKey = formatDate(dayjs());
  const todayTodos = todos.filter((todo) => todo.date === todayKey);

  //   
  const handleAdd = async (text, start, end, date) => {
    const newTodo = {
      text,
      completed: false,
      date: date,
      start, //  
      end,   //  
    };
    try {
      const res = await axios.post(API_URL, newTodo);
      setTodos([...todos, res.data]);
    } catch (e) {
      alert('   .');
    }
  };

  //      
  const handleToggle = async (id) => {
    const todo = todos.find((t) => t.id === id);
    if (!todo) return;
    try {
      const res = await axios.patch(`${API_URL}/${id}`, { completed: !todo.completed });
      setTodos(todos.map((t) => (t.id === id ? { ...t, completed: res.data.completed } : t)));
    } catch (e) {
      alert('  .');
    }
  };

  //    
  const handleDelete = async (id) => {
    try {
      await axios.delete(`${API_URL}/${id}`);
      setTodos(todos.filter((t) => t.id !== id));
    } catch (e) {
      alert(' .');
    }
  };

  return (
    <div className="app-container">
      <h1 className="app-title">To do List</h1>
      <TodoForm onAdd={handleAdd} />
      {loading ? (
        <div style={{textAlign:'center', color:'#6366f1', marginTop:20}}> ...</div>
      ) : (
        <TodoList todos={todos} onToggle={handleToggle} onDelete={handleDelete} />
      )}
      <TodayTimeline todos={todayTodos} />
    </div>
  );
}

export default App;
